/*start-checkbox*/
.checkbox {
  padding-left: 25px;
  color: #b6b6b6;
  cursor: pointer;
  position: relative;
  font-size: 12px;
}
.checkbox:last-child {
  margin-bottom: 0;
}
.checkbox input {
  position: absolute;
  left: -9999px;
}
.checkbox i {
  position: absolute;
  bottom: 7px;
  left: 0px;
  display: block;
  width: 19px;
  height: 20px;
  outline: none;
  border: 3px solid #df1e1c;
  background: #fff;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
}
.checkbox input + i:after {
  position: absolute;
  opacity: 0;
  transition: opacity 0.1s;
  -o-transition: opacity 0.1s;
  -ms-transition: opacity 0.1s;
  -moz-transition: opacity 0.1s;
  -webkit-transition: opacity 0.1s;
}
.checkbox input + i:after {
  content: "";
  background: url("../images/tick-mark1.png") no-repeat center;
  top: -6px;
  left: 0px;
  width: 18px;
  height: 18px;
  text-align: center;
}
.form-elements li:nth-child(2) {
  margin-left: 18px;
  width: 37%;
}
.checkbox input:checked + i:after {
  opacity: 1;
}
.checkbox input:checked + i {
  border: 3px solid #df1e1c;
  background: #fff;
}
